	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/4/the-new-york-times-gets-its-pulitzer-for-picking-on-apple/index.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>It didn’t matter that every major U.S. electronics company assembles its products under the same working conditions — or worse. Or that Apple was actually doing something about them. (Tim Cook called the Times’ implication that Apple didn’t care what happened to its subcontractors’ workers “patently false and offensive.“)</p>\n<p>The fact is, the New York Times knows how to win Pulitzers — better than any other journalistic operation. It has now won a record 112. It employs editors who specialize in identifying Pulitzer-winning topics and assigning reporters who will bring them home.</p>\n<p>And that’s what it set out to do — with Apple as its conspicuous subject — in seven major stories capped with a self-serving kicker that suggested that it was Times’ reporting that led to substantive changes in the working conditions in China’s electronics factories:</p>\n</blockquote>\n<p>I guess winning is a higher priority than being correct.</p>","frontmatter":{"title":"The New York Times gets its Pulitzer for picking on Apple","date":"April 17, 2013","passthroughUrl":"http://tech.fortune.cnn.com/2013/04/16/apple-pulitzer-ny-times/"}}},"pathContext":{"slug":"/blog/2013/4/the-new-york-times-gets-its-pulitzer-for-picking-on-apple/","previous":{"fields":{"slug":"/blog/2013/4/dont-be-a-padhole/"},"frontmatter":{"title":"Don't be a Padhole","passthroughUrl":"http://thebestpageintheuniverse.net/c.cgi?u=padhole","type":null}},"next":{"fields":{"slug":"/blog/2013/4/jony-ive-paints-a-fresh-yet-familiar-look-for-ios-7-9to5mac/"},"frontmatter":{"title":"Very, very flat","passthroughUrl":"http://9to5mac.com/2013/04/29/jony-ive-paints-a-fresh-yet-familiar-look-for-ios-7/","type":null}}}}

